import { css, modifier, styled, variants } from '@lokalise/styled';

import type { Appearance } from './IconButton';
import { spacing, value } from '../../utils/theme';
import { Pressable } from '../Pressable';

type IconButtonProps = {
	$appearance: Appearance;
	$active?: boolean;
	disabled?: boolean;
	$isLoading: boolean;
};

const variantColorStyles = css<IconButtonProps>`
	${variants('$appearance', {
		primary: css`
			padding: 6px;
			font-size: 20px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.active')};
					color: ${value('color.text.active')};
				`,
			)}
		`,
		secondary: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.active')};
					color: ${value('color.text.active')};
				`,
			)}
		`,
		verified: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.warning')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.border.warning')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.warning')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.warning')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
		reviewed: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.success')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.border.success')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.success')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.success')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
	})}
`;

export const Container = styled(Pressable)<IconButtonProps>`
	display: inline-flex;
	align-items: center;
	background-color: transparent;
	transition:
		background-color ${value('transition.default')},
		color ${value('transition.default')};
	border: 0;
	border-radius: ${value('radius.m')};
	text-decoration: none;

	${variantColorStyles}

	// disabled
    &:disabled {
		cursor: not-allowed;
		color: ${value('color.text.disabled')};

		${modifier(
			'$isLoading',
			css<IconButtonProps>`
				color: initial;
				cursor: auto;
				${variantColorStyles}
			`,
		)}
	}
`;
